<template>

  <div class="login-demo">
    <div class="login-wrap">
      <el-row type="flex" justify="center">
        <!-- 登录框 -->
        <el-form ref="loginForm" :rules="formRules" :model="user" status-icon label-width="70px">
          <el-tabs :stretch="true" v-model="activeName" @tab-click="handleClick">
            <el-tab-pane label="登录" name="login">
              <el-form-item prop="username" label="用户名">
                <el-input v-model="user.username" placeholder="请输入用户名"></el-input>
              </el-form-item>
              <el-form-item prop="password" label="密码">
                <el-input v-model="user.password" show-password placeholder="请输入密码">
                  <template slot="prepend"></template>
                </el-input>
              </el-form-item>

              <br>
 
              <el-form-item>
                <el-button type="primary" @click="doLogin('loginForm')">登 录</el-button>
              </el-form-item>
            </el-tab-pane>
          </el-tabs>
        </el-form>
      </el-row>
    </div>
  </div>
</template>
 
<script>
import axios from "axios";
export default {
  name: "login",
  data() {
    return {
      activeName: "login",
      checked: false,
      user: {
        username: "",
        password: ""
      },
      // 通过 formRules 属性传入约定的验证规则
      formRules: {
        username: [{ required: true, message: "用户名不能为空", trigger: "blur" }],
        password: [
          { required: true, message: "密码不能为空", trigger: "blur" },
          { type: "string", min: 6, message: "密码不能小于6位", trigger: "blur" }
        ],
         email: [
           { type: "email", message: "请输入正确的邮箱地址", trigger: ["blur", "change"] }
        ]
      }
    }
  },
  created() {},
  methods: {
    // 登录
    doLogin(form) {
      this.$refs[form].validate((valid) => {
        if (valid) {
           this.$router.push({ path: "/backhome" });
          // axios
          //   .post("/submitLogin/", {
          //     // .post("/api/login/", {
          //     name: this.user.username,
          //     password: this.user.password
          //   })
          //   .then(res => {
          //      if (res.data.status === 200) {
          //       this.$router.push({ path: "/welcome" });
          //     } else {
          //       this.$message.error(res.data.msg)
          //     }
          //   });
        } else {
          return false;
        }
      });
    },
    // 注册账号
    doRegister(form) {
      this.$refs[form].validate((valid) => {
        if (valid) {
          console.log(valid);
          axios
            .post("/submitRegister/", {
              name: this.user.username,
              email: this.user.email,
              password: this.user.password
            })
            .then(res => {
              // console.log("输出response.data", res.data);
              if (res.data.status === 200) {
                this.$message.success(res.data.msg)
                this.activeName = "login";
              } else {
                this.$message.error(res.data.msg);
              }
            });
        } else {
          return false;
        }
      });
    },
    handleClick(tab, event) {
      console.log(tab, event);
    },
    // 记住密码
     
  }
}
 
  
</script>
<style scoped>
/* 公共样式 */
li {
  list-style: none;
  float: left;
}
 
.login-demo {
  position: fixed;
  width: 100%;
  height: 100%;
  background: url("./image/13.gif") no-repeat;
  background-size: cover;
  top: 0px;
  left: 0px;
  overflow: hidden;
}
 
.login-wrap {
/* background: linear-gradient(90deg, rgba(239,131,89,1) 0%, rgba(9,9,121,1) 0%, rgba(239,131,89,0.26094187675070024) 0%); */
 width: 400px;
  height: 310px;
  margin: 250px auto;
  margin-left: 1000px;
  border-radius: 6px;
  line-height: 20px;
  padding-top: 0px;
}

 
.el-tabs__nav {
  font-size: 18px;
}
 
a {
  text-decoration: none;
  color: #606266;
  font-size: 14px;
  float: right;
}
 
a:hover {
  color: coral;
}
 
.el-checkbox {
  text-indent: 4px;
}
 
 
 
.el-button {
  width: 118%;
  margin-left: -40px;
  background-color:
    #16696a;
  letter-spacing: 5px;
  border: 0px;
}
 
 
</style>
 